html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 1.8rem;
}

.contenedor {
  width: 95%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.6rem;
}

h4 {
  font-size: 2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-naranja-block {
  background-color: #E08709;
  border: 1px solid rgb(174.9699570815, 105.4506437768, 7.0300429185);
  color: #FFFFFF;
  font-weight: bold;
  padding: 0.7rem 2rem;
  text-align: center;
  display: block;
}
.boton-naranja-block:hover {
  background-color: rgb(199.4849785408, 120.2253218884, 8.0150214592);
  border: 1px solid rgb(245.2854077253, 149.2188841202, 13.2145922747);
  cursor: pointer;
}
.boton-naranja-block:active {
  background-color: rgb(174.9699570815, 105.4506437768, 7.0300429185);
  border: 1px solid rgb(246.2703862661, 159.9442060086, 37.7296137339);
}

.boton-naranja {
  background-color: #E08709;
  border: 1px solid rgb(174.9699570815, 105.4506437768, 7.0300429185);
  color: #FFFFFF;
  font-weight: bold;
  padding: 0.7rem 2rem;
  text-align: center;
  display: inline-block;
}
.boton-naranja:hover {
  background-color: rgb(199.4849785408, 120.2253218884, 8.0150214592);
  border: 1px solid rgb(245.2854077253, 149.2188841202, 13.2145922747);
  cursor: pointer;
}
.boton-naranja:active {
  background-color: rgb(174.9699570815, 105.4506437768, 7.0300429185);
  border: 1px solid rgb(246.2703862661, 159.9442060086, 37.7296137339);
}

.boton-verde {
  background-color: #71B100;
  border: 1px solid rgb(80.4406779661, 126, 0);
  color: #FFFFFF;
  font-weight: bold;
  padding: 0.7rem 2rem;
  text-align: center;
  display: inline-block;
  margin-top: 2rem;
}
.boton-verde:hover {
  background-color: rgb(96.7203389831, 151.5, 0);
  border: 1px solid rgb(129.2796610169, 202.5, 0);
  cursor: pointer;
}
.boton-verde:active {
  background-color: rgb(80.4406779661, 126, 0);
  border: 1px solid rgb(145.5593220339, 228, 0);
}

/*Acá convertimos todo el background del body en negro y ver que colores cambiar en las distintas secciones
para que se vea bien el contenido*/
.dark-mode {
  background-color: rgb(25.5, 25.5, 25.5);
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .contenido-anuncio h3,
.dark-mode .resumen-propiedad .contenido-anuncio h3 {
  color: #e1e1e1;
}
.dark-mode .anuncio .iconos-caracteristicas p,
.dark-mode .resumen-propiedad .iconos-caracteristicas p {
  color: #FFFFFF;
}
.dark-mode .anuncio .iconos-caracteristicas .icono,
.dark-mode .resumen-propiedad .iconos-caracteristicas .icono {
  filter: invert(100%);
}
.dark-mode .formulario legend, .dark-mode .formulario label, .dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}

.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 45rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 45rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  max-width: 60rem;
  font-size: 3.5rem;
}

.footer {
  background-color: #333333;
  padding: 2rem;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 1.2rem;
}
.footer .navegacion {
  opacity: 1; /*Acá tenemos que tener siempre el opacity: 1 ya que la navegacion del footer siempre debe estar*/
  max-height: 144px;
}
.footer .copyright {
  text-align: center;
  color: #FFFFFF;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-bottom: 1.5rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}

.barra {
  padding-top: 1rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: end;
  }
}
@media (min-width: 768px) {
  .barra a {
    padding-top: 0;
    transition: color 0.3s;
  }
}

.navegacion {
  opacity: 0;
  max-height: 0;
  transition: opacity 0.5s ease-in-out, max-height 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
}
.navegacion a:last-child {
  margin-right: 0;
}
.navegacion a:hover {
  color: rgb(245.2854077253, 149.2188841202, 13.2145922747);
  padding: 0;
}

.mobile-menu {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  margin-bottom: 1rem;
  cursor: pointer;
}

.mostrar {
  /*display: flex;
  flex-direction: column;
  @include tablet {
      flex-direction: row;
  }*/
  opacity: 1;
  max-height: 144px;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}
.icono p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: rgb(237.75, 237.75, 237.75);
  border: 1px solid rgb(212.25, 212.25, 212.25);
  margin-bottom: 2rem;
}
.anuncio:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio .contenido-anuncio h3, .anuncio .contenido-anuncio p {
  margin: 0rem;
}
.anuncio .contenido-anuncio h3 {
  font-size: 2.2rem;
  color: #000000;
}
.anuncio .contenido-anuncio p {
  font-size: 1.8rem;
}
@media (min-width: 1024px) {
  .anuncio .contenido-anuncio h3 {
    font-size: 2.5rem;
  }
  .anuncio .contenido-anuncio p {
    font-size: 2.2rem;
  }
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  gap: 4rem;
  padding: 0;
  justify-content: center;
}
.iconos-caracteristicas li {
  display: flex;
}
.iconos-caracteristicas li img {
  width: 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  color: #000000;
  font-weight: bold;
}

.centrar-btn {
  display: flex;
  justify-content: center;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 7rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
}
.imagen-contacto h2 {
  font-weight: 900;
}
.imagen-contacto p {
  font-weight: 300;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-child {
  margin-bottom: 0;
}
.entrada-blog .imagen picture {
  height: 100%;
}
.entrada-blog .imagen picture img {
  height: 90%;
}

.texto-entrada a {
  color: #000000;
  display: block;
  margin-top: 1.2rem;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
    display: inline-block;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.2;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 12rem;
  height: 0.3rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.informacion-meta span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  color: #FFFFFF;
  padding: 2.2rem;
  font-size: 2rem;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 4rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 4rem;
  height: 6rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  font-size: 1.6rem;
  font-weight: bold;
  margin: 0;
  text-align: right;
  display: block;
  margin-right: 0.8rem;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  display: block;
  font-weight: bold;
  text-transform: uppercase;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.formulario fieldset {
  border: 2px solid #e1e1e1;
}

.forma-contacto {
  max-width: 25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.forma-contacto input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 1.8rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem;
}

.resumen-propiedad .precio {
  font-size: 2.6rem;
}
.resumen-propiedad .iconos-caracteristicas {
  display: flex;
  justify-content: flex-start;
  gap: 10rem;
}
.resumen-propiedad p {
  font-size: 1.8rem;
  margin: 1rem 0;
}